<template>
    <div>
        <drawer
            :title="''"
            :disabled="drawerDisabled"
            @close-drawer="drawerDisabled = true"
        >
            <anchors-view :list="anchorList"/>
        </drawer>
    </div>
</template>

<script>
import { h } from "vue"
import Drawer from "./drawer"
import EventBus from "../common/EventBus"

export default {
    data() {
        return {
            drawerDisabled: true,
            anchorList: []
        }
    },
    components: {
        Drawer,
        AnchorsView: {
            props: ["list"],
            render() {
                let childrenList =
                    this.list.map((item) => {
                        return h(
                            "a", {
                                level: item.level,
                                href: "#" + item.target
                            }, item.content
                        )
                    })

                return h("div", childrenList)
            }
        }
    },
    inject: ["note"],
    methods: {
        // 
        getAnchors(nodeList, level, index, callback) {
            let list = []
            for (let i in nodeList) {
                const item = nodeList[i]
                i = index + i
                // 
                if (item.NT == "h") {
                    list.push({
                        content: item.CT,
                        target: i,
                        level
                    })
                } else if (item.NT == "floor") {
                    // level  4
                    const nextLevel = level < 4 ?
                                      level+1 : level

                    const li = this.getAnchors(
                        item.CTS, nextLevel, i
                    )
                    list.push(...li)
                }
            }
            // 
            if (level == 1) {
                callback(list)
            } else {
                return list
            }
        },
        close() {
            this.drawerDisabled = true
        }
    },
    mounted() {
        EventBus.on("open-anchors", () => {
            this.drawerDisabled = false

            this.getAnchors(
                this.note.CTS, 1, "", (list) => {
                    this.anchorList = list
                }
            )
        })
    }
}
</script>

<style scoped>
    .fullscreen-mask {
        cursor: pointer;
    }
</style>